// 示例组件
import Examples from './Examples';
import AppOptionsEg from './AppOptionsEg';
import AppTableEg from './AppTableEg';
import AppMenuEg from './AppMenuEg';

export default {
  path: '/examples',
  label: '示例',
  component: Examples,
  hidden: true, // 隐藏  浏览器导航栏可以进入
  children: [
    {
      path: 'app-options-eg',
      component: AppOptionsEg
    },
    {
      path: 'app-table-eg',
      component: AppTableEg
    },
    {
      path: 'app-menu-eg',
      component: AppMenuEg
    },
    {
      path: '',
      redirect: 'app-options-eg'
    }
  ]
};



