import store from '../../../store/index';

// const UserList = () => import('./UserList.vue');

// const UserList = (resolve) => {
//   import('./UserList.vue').then((module) => {
//     resolve(module);
//   });
// }

const UserList = resolve => {
  require.ensure(['./UserList.vue'], () => {
    resolve(require('./UserList.vue'));
  }, 'user');
}
export default {
  path: '/settings',
  name: 'Settings',
  icon: 'fa-users',
  label: '用户列表',
  component: UserList,
  permission: [99],
  // 避免直接通过浏览器导航栏进入
  beforeEnter: (to, from, next) => {
    const userMsg = store.state.userMsg;
    const permission = [99];
    permission.includes(userMsg.role_id) ? next() : next({
      path: '/auth'
    });
  }
};



